@import url('https://fonts.googleapis.com/css?family=Lato');

body {
    font-family: 'Open Sans', sans-serif;
    background-color: #efefef;
}

strong {
    font-weight: 900;
}

.description_modal {
    font-weight: 100;
    font-size: 14px;
}

.view-container{
    margin-top:20px;
}

.thumbnail .caption{
    position:relative;
    z-index:40;
}

.header h2{
    float: left;
    text-transform: uppercase;
    margin: 40px 0;
    font-weight: 600;
}

.header h2:nth-child(1){
    color: #fcc453;
}

.header h2:nth-child(2){
    color: #878787;
}

.header {
    padding-left: 15px;
}

.caption {
    overflow: hidden;
    line-height: 0;
}

.caption h2 {
    font-size: 16px;
    font-weight: 800;
    color: #fcc453;
    margin-top: 0;
}

.caption p, .details-header p {
    font-size: 12px;
    color: #b5b5b5;
}

.thumbnail img {
    display: inline-block;
    max-height: 200px;
}

.ratings {
    padding-right: 10px;
    padding-left: 10px;
    color: #d17581;
}

.img-thumbnail {
    border: none;
}

.thumbnail {
    padding: 5px;
    float: none;
    width: 100%;
    text-align: center;
    -webkit-transition-duration: .3s;
         -o-transition-duration: .3s;
            transition-duration: .3s;
    -webkit-transition-property: -webkit-transform;
    transition-property: -webkit-transform;
    -o-transition-property: transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform;
}

.thumbnail:hover {
    -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
            transform: scale(1.05);
}

footer {
    margin: 50px 0;
}

.modal-background {
    position: fixed;
    background: rgba(0, 0, 0, 0.2);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 999;
}

.description_modal {
    margin: 10%;
    background-color: #fff;
    border-radius: 5px;
}

.description_modal {
    padding: 5% 2%;
}

.details-thumbnail {
    text-align: center;
}

.img-details {
    max-height: 300px;
}

.properties:nth-child(2) {
    margin: 0 10px;
}

.details-header h3 {
    margin: 0;
}

.details-bottom p {
    margin: 0 0 5px 0;
}

.details-properties, .details-bottom{
    margin: 20px 0;
}

.spinner-container {
    width: 100%;
    height: 200px;
    float: left;
    text-align: center;
    position: relative;
}

.spinner {
    border-radius: 50%;
    margin: 5% auto;
    width: 32px;
    height: 32px;
    border: 0.25rem solid rgba(8,8,8, 0.9);
    border-top-color: rgba(8,8,8, 0.2);
    -webkit-animation: spin 1s infinite linear;
            animation: spin 1s infinite linear;
}

@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
@keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
